import { useState } from "react";
import { useSelector, useDispatch } from "react-redux";

import { Box, Grid, ResponsiveContext, DataTable, Text } from "grommet";
import { Chat, Copy } from "grommet-icons";

import Card from "../../components/Card";
import { showDoc } from "../dash/dashStore";

const DashSide = () => {
  const dash = useSelector((state) => state.dash);
  const docs = useSelector((state) => state.docs);
  const dispatch = useDispatch()

  const [select, onSelect] = useState(null);

  const columns = {
    messages: [{
      property: "body",
      primary: true,
      header: <Text>Messages</Text>,
      render: (datum) => datum.body.substring(0, 25) + "...",
    }],
    files: [{
      property: "body",
      primary: true,
      header: <Text>Documents</Text>,
      render: (datum) => datum.name.replace('_',' ').replace(/\.[^.]+$/, "").substring(0, 25),
    }],
  } 
    


  const { messages } = dash;
  const { files } = docs;

  return (
    <>
      <Box pad={{ top: "xlarge" }} />
      <ResponsiveContext.Consumer>
        {(size) => (
          <Grid
            gap="medium"
            columns={
              size === "small"
                ? { count: "fit", size: "small" }
                : { count: "fill", size: "full" }
            }
            justify="center"
            responsive
          >
            <Card
              title={
                <>
                  <Chat size="large" />
                  <Text>{" " + messages.length}</Text>
                </>
              }
              subTitle=""
              align="start"
              children={
                <DataTable
                  columns={columns.messages}
                  data={messages}
                  step={10}
                  select={select}
                  onSelect={onSelect}
                  onClickRow={() => null}
                />
              }
              footer=""
              size="large"
            />
            <Card
              title={
                <>
                  <Copy size="large" />
                  <Text>{" " + files.length}</Text>
                </>
              }
              subTitle=""
              align="start"
              children={
                files &&
                  <DataTable
                  columns={columns.files}
                  data={files}
                  step={10}
                  select={select}
                  onSelect={onSelect}
                  onClickRow={({datum}) => dispatch(showDoc(datum.url))}
                />

                    // label={document.name.substring(0, 15)}
                    // href={document.url} 
          
              
              }
              footer=""
              size="large"
            />
          </Grid>
        )}
      </ResponsiveContext.Consumer>
    </>
  );
};

export default DashSide;
