import { createSlice } from "@reduxjs/toolkit";
import firebase from "../../firebase";
const store = firebase.storage();

const slice = createSlice({
  name: "docs",
  initialState: {
    user: "x",
    error: null,
    files: [
      { 
        name:"example_register.pdf",
        url:"https://firebasestorage.googleapis.com/v0/b/docs-conveyanceme/o/x%2Fexample_register.pdf?alt=media&token=4fed4a2b-3aa3-40c9-910d-86754413020f"
      }],
  },
  reducers: {
    uploadProgress: (state, { payload }) => {
      const { _filename, _progress } = payload;
      state[_filename] = _progress;
    },
    uploadURL: (state, { payload }) => {
      state.files.push(payload)
    },
    uploadFail: (state, { payload }) => {
      state.error = payload.error;
    },
  },
});
export default slice.reducer;

const { uploadFail, uploadURL, uploadProgress } = slice.actions;
export const uploadFile = (file) => {
  return async (dispatch) => {
    let fileRef = store.ref("x/" + file.name)
    let uploader = fileRef.put(file)
    try {
      uploader.on(
        "state_changed",

        ({ ref, bytesTransferred, totalBytes }) => {
          let _progress = Math.round((bytesTransferred / totalBytes) * 100);
          let _filename = ref.name;
          dispatch(uploadProgress({ _filename, _progress }));
        }
      , {}, ()=> fileRef.getDownloadURL().then(url => dispatch(uploadURL({name: file.name, url})))
      )
    } catch (error) {
      dispatch(uploadFail(error));
    }
  };
};
